[data-netlify-identity-menu] li:first-child {
    margin-right: 16px;
}

[data-netlify-identity-menu] li {
    list-style-type: none;
    display:inline;
}

.customNetlify {
  border: 1px;
  border-radius: 20px;
}

.netlify-identity-user-details {
    display: inline;
    font-size: smaller;
    border: 1px solid gray;
    border-radius: 4px;
    width: 192px;
    height: 24px;
    padding: 8px;
    outline: 0;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    opacity: 0.3;
}

.netlify-identity-user {
    display: inline;
}

[data-netlify-identity-menu] a {
    //display: block;
    //position: relative;
    width: 192px;
    height: 24px;
    padding: 8px;
    outline: 0;
    cursor: pointer;
    border: 1px solid gray;
    border-radius: 4px;
    //color: gray;
    transition: background-color 0.2s ease;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
}

.github-link {
    font-weight: 500;
    font-size: 16px;
    color: inherit;
    text-decoration: none;
}

.github-link:hover,
.github-link:focus {
    text-decoration: underline;
}

.github-link::before {
    display: inline-block;
    position: relative;
    top: 4px;
    content: " ";
    width: 19px;
    height: 19px;
    margin-right: 8px;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(/static/img/landing/GitHub-Mark-120px-plus.png);
}

@media (max-width: 880px) {
    .main {
        padding: 16px;
    }

    .header h1 {
        font-size: 10vw;
    }

    .hands-on-area {
        flex-direction: column;
    }

    [data-netlify-identity-menu] ul {
        display: flex;
        flex-direction: column;
    }

    [data-netlify-identity-menu] li {
        margin-bottom: 16px;
    }
}

@media screen and (max-height: 680px) {
    .main {
        padding: 64px 0;
    }

    .footer {
        position: relative;
        bottom: auto;
    }
}